'use client'

import { useState, useEffect } from 'react'
import { useAuth } from '@/context/AuthContext'
import { communicationAPI, type Message, type Conversation } from '@/lib/api/communication'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Separator } from '@/components/ui/separator'
import { Textarea } from '@/components/ui/textarea'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Checkbox } from '@/components/ui/checkbox'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import Link from 'next/link'
import { toast } from 'sonner'

export default function CommunicationPage() {
  const { user } = useAuth()
  const [announcements, setAnnouncements] = useState<Message[]>([])
  const [conversations, setConversations] = useState<Conversation[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')

  useEffect(() => {
    loadData()
  }, [])

  const loadData = async () => {
    try {
      setLoading(true)
      setError('')
      const [announcementsData, conversationsData] = await Promise.all([
        communicationAPI.getAnnouncements(),
        communicationAPI.getConversations()
      ])
      setAnnouncements(announcementsData)
      setConversations(conversationsData)
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to load data')
    } finally {
      setLoading(false)
    }
  }

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'high': return 'destructive'
      case 'medium': return 'default'
      case 'low': return 'secondary'
      default: return 'outline'
    }
  }

  const getTypeColor = (type: string) => {
    switch (type) {
      case 'emergency': return 'destructive'
      case 'announcement': return 'default'
      case 'message': return 'secondary'
      default: return 'outline'
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-background">
        <div className="container mx-auto px-4 py-8">
          <div className="text-center">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
            <p className="text-muted-foreground">Loading communication center...</p>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <header className="border-b bg-card">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-2xl font-bold">Communication Center</h1>
              <p className="text-muted-foreground">Announcements, messages, and notifications</p>
            </div>
            <div className="flex gap-2">
              {user?.role && ['committee', 'super_admin'].includes(user.role) && (
                <NewAnnouncementDialog onSuccess={loadData} />
              )}
              <Link href="/">
                <Button variant="outline">Back to Dashboard</Button>
              </Link>
            </div>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        {error && (
          <Alert variant="destructive" className="mb-6">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        <Tabs defaultValue="announcements" className="space-y-6">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="announcements">
              Announcements ({announcements.length})
            </TabsTrigger>
            <TabsTrigger value="messages">
              Messages ({conversations.filter(c => c.unreadCount > 0).length} unread)
            </TabsTrigger>
          </TabsList>

          <TabsContent value="announcements" className="space-y-4">
            <div className="space-y-4">
              {announcements.map((announcement) => (
                <AnnouncementCard 
                  key={announcement.id} 
                  announcement={announcement}
                  currentUserId={user?.id || ''}
                />
              ))}
              {announcements.length === 0 && (
                <Card>
                  <CardContent className="text-center py-8">
                    <p className="text-muted-foreground">No announcements available</p>
                  </CardContent>
                </Card>
              )}
            </div>
          </TabsContent>

          <TabsContent value="messages" className="space-y-4">
            <div className="space-y-4">
              {conversations.map((conversation) => (
                <ConversationCard 
                  key={conversation.id} 
                  conversation={conversation}
                  currentUserId={user?.id || ''}
                />
              ))}
              {conversations.length === 0 && (
                <Card>
                  <CardContent className="text-center py-8">
                    <p className="text-muted-foreground">No conversations available</p>
                    <Button className="mt-4">Start New Conversation</Button>
                  </CardContent>
                </Card>
              )}
            </div>
          </TabsContent>
        </Tabs>
      </main>
    </div>
  )
}

function AnnouncementCard({ announcement, currentUserId }: { 
  announcement: Message
  currentUserId: string 
}) {
  const isRead = announcement.readBy.includes(currentUserId)
  
  return (
    <Card className={!isRead ? 'border-primary' : ''}>
      <CardHeader>
        <div className="flex justify-between items-start">
          <div className="flex-1">
            <div className="flex items-center gap-2 mb-2">
              <CardTitle className="text-lg">{announcement.subject}</CardTitle>
              {!isRead && <Badge variant="secondary" className="text-xs">New</Badge>}
            </div>
            <CardDescription>
              From: {announcement.sender.name} • {new Date(announcement.createdAt).toLocaleDateString()}
            </CardDescription>
          </div>
          <div className="flex gap-2">
            <Badge variant={announcement.type === 'emergency' ? 'destructive' : 'default'}>
              {announcement.type}
            </Badge>
            <Badge variant={announcement.priority === 'high' ? 'destructive' : 
                           announcement.priority === 'medium' ? 'default' : 'secondary'}>
              {announcement.priority}
            </Badge>
          </div>
        </div>
      </CardHeader>
      <CardContent>
        <p className="text-sm mb-4">{announcement.content}</p>
        <Separator className="my-3" />
        <div className="flex justify-between items-center text-xs text-muted-foreground">
          <div className="flex gap-2">
            <span>Channels:</span>
            {announcement.channels.map((channel) => (
              <Badge key={channel} variant="outline" className="text-xs">
                {channel}
              </Badge>
            ))}
          </div>
          <span>Read by {announcement.readBy.length} residents</span>
        </div>
      </CardContent>
    </Card>
  )
}

function ConversationCard({ conversation, currentUserId }: { 
  conversation: Conversation
  currentUserId: string 
}) {
  const otherParticipant = conversation.participants.find(p => p.id !== currentUserId)
  
  return (
    <Card className={conversation.unreadCount > 0 ? 'border-primary' : ''}>
      <CardHeader>
        <div className="flex justify-between items-start">
          <div>
            <CardTitle className="text-lg">
              {otherParticipant?.name || 'Unknown'}
            </CardTitle>
            <CardDescription>
              {otherParticipant?.role} • {new Date(conversation.lastMessage.timestamp).toLocaleDateString()}
            </CardDescription>
          </div>
          {conversation.unreadCount > 0 && (
            <Badge variant="destructive">{conversation.unreadCount}</Badge>
          )}
        </div>
      </CardHeader>
      <CardContent>
        <p className="text-sm text-muted-foreground mb-4">
          {conversation.lastMessage.content}
        </p>
        <div className="flex gap-2">
          <Button variant="outline" size="sm">
            View Conversation
          </Button>
          <Button variant="outline" size="sm">
            Reply
          </Button>
        </div>
      </CardContent>
    </Card>
  )
}

function NewAnnouncementDialog({ onSuccess }: { onSuccess: () => void }) {
  const [open, setOpen] = useState(false)
  const [loading, setLoading] = useState(false)
  const [formData, setFormData] = useState({
    subject: '',
    content: '',
    type: 'announcement' as const,
    priority: 'medium' as const,
    channels: ['email'] as ('email' | 'sms' | 'push')[]
  })

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!formData.subject || !formData.content) {
      toast.error('Please fill in all required fields')
      return
    }

    try {
      setLoading(true)
      await communicationAPI.sendAnnouncement({
        ...formData,
        sender: { id: 'current_user', name: 'Society Committee', role: 'committee' },
        recipients: ['all']
      })
      toast.success('Announcement sent successfully!')
      setOpen(false)
      setFormData({
        subject: '',
        content: '',
        type: 'announcement',
        priority: 'medium',
        channels: ['email']
      })
      onSuccess()
    } catch (error) {
      toast.error('Failed to send announcement')
    } finally {
      setLoading(false)
    }
  }

  const handleChannelChange = (channel: 'email' | 'sms' | 'push', checked: boolean) => {
    if (checked) {
      setFormData(prev => ({
        ...prev,
        channels: [...prev.channels, channel]
      }))
    } else {
      setFormData(prev => ({
        ...prev,
        channels: prev.channels.filter(c => c !== channel)
      }))
    }
  }

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        <Button>New Announcement</Button>
      </DialogTrigger>
      <DialogContent className="max-w-2xl">
        <DialogHeader>
          <DialogTitle>Create New Announcement</DialogTitle>
          <DialogDescription>
            Send an announcement to all residents
          </DialogDescription>
        </DialogHeader>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="subject">Subject *</Label>
            <Input
              id="subject"
              value={formData.subject}
              onChange={(e) => setFormData(prev => ({ ...prev, subject: e.target.value }))}
              placeholder="Enter announcement subject"
              required
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="content">Content *</Label>
            <Textarea
              id="content"
              value={formData.content}
              onChange={(e) => setFormData(prev => ({ ...prev, content: e.target.value }))}
              placeholder="Enter announcement content"
              rows={4}
              required
            />
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="type">Type</Label>
              <Select value={formData.type} onValueChange={(value: any) => setFormData(prev => ({ ...prev, type: value }))}>
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="announcement">Announcement</SelectItem>
                  <SelectItem value="emergency">Emergency</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <Label htmlFor="priority">Priority</Label>
              <Select value={formData.priority} onValueChange={(value: any) => setFormData(prev => ({ ...prev, priority: value }))}>
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="low">Low</SelectItem>
                  <SelectItem value="medium">Medium</SelectItem>
                  <SelectItem value="high">High</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>

          <div className="space-y-2">
            <Label>Notification Channels</Label>
            <div className="flex gap-4">
              <div className="flex items-center space-x-2">
                <Checkbox
                  id="email"
                  checked={formData.channels.includes('email')}
                  onCheckedChange={(checked) => handleChannelChange('email', checked as boolean)}
                />
                <Label htmlFor="email">Email</Label>
              </div>
              <div className="flex items-center space-x-2">
                <Checkbox
                  id="sms"
                  checked={formData.channels.includes('sms')}
                  onCheckedChange={(checked) => handleChannelChange('sms', checked as boolean)}
                />
                <Label htmlFor="sms">SMS</Label>
              </div>
              <div className="flex items-center space-x-2">
                <Checkbox
                  id="push"
                  checked={formData.channels.includes('push')}
                  onCheckedChange={(checked) => handleChannelChange('push', checked as boolean)}
                />
                <Label htmlFor="push">Push Notification</Label>
              </div>
            </div>
          </div>

          <div className="flex justify-end gap-2 pt-4">
            <Button type="button" variant="outline" onClick={() => setOpen(false)}>
              Cancel
            </Button>
            <Button type="submit" disabled={loading}>
              {loading ? 'Sending...' : 'Send Announcement'}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  )
}
